import * as vscode from 'vscode';
import { metadataItems, apiItems } from './lib/items.mjs';
import { updateConfig } from './lib/updateConfig.mjs';
import path from 'path';

/* eslint-disable no-unused-vars */

/**
 * Activate the extension
 * @param {vscode.ExtensionContext} context - The extension context
 */
export function activate(context) {
  vscode.window.showInformationMessage('Congratulations, your extension "Monkey Wrench" is now active!');

  vscode.languages.setLanguageConfiguration('javascript', {
    wordPattern: /(-?\d*\.\d\w*)|([^\`\~\!\#\%\^\&\*\(\)\-\=\+\[\{\]\}\\\|\;\:\'\"\,\.\<\>\/\?\s]+)/g,
    onEnterRules: [
      {
        beforeText: /^\/\/ (?:@|==UserScript==).*$/,
        action: {
          indentAction: vscode.IndentAction.None,
          appendText: '// '
        }
      }
    ]
  });

  commandDisposables.push(
    vscode.commands.registerCommand('monkey-wrench.helloWorld', () => {
      vscode.window.showInformationMessage('Hello World from Monkey Wrench!');
    }),
    vscode.commands.registerCommand('monkey-wrench.updateConfigs', async () => {
      if (config.get('enableTsConfigUpdate')) {
        updateConfig('tsconfig');
      }
      if (config.get('enableJsConfigUpdate')) {
        updateConfig('jsconfig');
      }
      if (config.get('enableEslintConfigUpdate')) {
        updateConfig('.eslintrc');
      }
    })
  );

  configDisposables.push(vscode.workspace.onDidChangeConfiguration(e => {
    if (e.affectsConfiguration('monkey-wrench.enableTsConfigUpdate')) {
      updateConfig('tsconfig');
    }
    if (e.affectsConfiguration('monkey-wrench.enableJsConfigUpdate')) {
      updateConfig('jsconfig');
    }
    if (e.affectsConfiguration('monkey-wrench.enableEslintConfigUpdate')) {
      updateConfig('.eslintrc');
    }
  }));

  // apiItems && metadataItems
  hoverProviders.push(
    vscode.languages.registerHoverProvider('javascript', {
      provideHover(document, position, token) {
        if (!allSystemsGo(document)) return;
        const wordRange = document.getWordRangeAtPosition(position);
        if (!wordRange) return;
        const word = document.getText(wordRange);
        const item = metadataItems.find(item => item.label === word && item.hover);
        if (!item) return;
        item.hover.range = wordRange;
        return item.hover;
      }
    }),
    vscode.languages.registerHoverProvider('javascript', {
      provideHover(document, position, token) {
        if (!allSystemsGo(document)) return;
        const wordRange = document.getWordRangeAtPosition(position);
        if (!wordRange) return;
        const word = document.getText(wordRange);
        const item = apiItems.find(item => item.label === word && item.hover);
        if (!item) return;
        item.hover.range = wordRange;
        return item.hover;
      }
    })
  );

  // apiItems && metadataItems
  completionItemProviders.push(
    vscode.languages.registerCompletionItemProvider('javascript', ApiCompletionItemProvider),
    vscode.languages.registerCompletionItemProvider('javascript', MetadataCompletionItemProvider),
    vscode.languages.registerCompletionItemProvider('javascript', MetadataParametersCompletionItemProvider, ' ')
  );

  // apiItems
  signatureHelpProviders.push(vscode.languages.registerSignatureHelpProvider('javascript', {
    provideSignatureHelp(document, position, token, context) {
      const line = document.lineAt(position.line).text;
      const functionCallStart = line.lastIndexOf('GM', position.character);
      const functionCall = line.substring(functionCallStart, position.character);
      const [functionName, parameterString] = functionCall.split('(');
      const parameters = parameterString.split(',');
      const parameterIndex = parameters.length - 1;
      const signatureHelp = new vscode.SignatureHelp();
      signatureHelp.signatures = apiItems.find(item => item.label === functionName).signatures;
      if (!signatureHelp.signatures.length) return;
      signatureHelp.activeSignature = 0;
      signatureHelp.activeParameter = parameterIndex;
      return signatureHelp;
    }
  }, { triggerCharacters: ['(', ','], retriggerCharacters: [','] }));

  // metadataItems
  signatureHelpProviders.push(vscode.languages.registerSignatureHelpProvider('javascript', {
    provideSignatureHelp(document, position, token, context) {
      if (!allSystemsGo(document)) return;
      const line = document.lineAt(position.line).text;
      const match = line.match(/@\w+/);
      const label = match[0];
      const labelIndex = match.index;
      const signatureHelp = new vscode.SignatureHelp();
      signatureHelp.signatures = metadataItems.find(item => item.label === label).signatures;
      if (!signatureHelp.signatures.length) return;
      signatureHelp.activeSignature = 0;
      let parameterIndex = line
        .substring(label.length + labelIndex)
        .replaceAll(/\s+/g, '|')
        .split('|').length - 2;
      if (parameterIndex > signatureHelp.signatures[0].parameters.length - 1) return;
      signatureHelp.activeParameter = parameterIndex;
      return signatureHelp;
    }
  }, { triggerCharacters: [' '], retriggerCharacters: [' '] }));

  definitionProviders.push(vscode.languages.registerDefinitionProvider('javascript', {
    provideDefinition(document, position, token) {
      const wordRange = document.getWordRangeAtPosition(position);
      const name = document.getText(wordRange);
  
      for (let line = 0; line < document.lineCount; line++) {
        const lineText = document.lineAt(line).text;
  
        if (lineText.includes(`function ${name}`)) {
          return new vscode.Location(document.uri, new vscode.Position(line, 0));
        }
      }
  
      return null;
    }
  }));

  typeDefinitionProviders.push(vscode.languages.registerTypeDefinitionProvider('javascript', {
    async provideTypeDefinition(document, position, token) {
      const wordRange = document.getWordRangeAtPosition(position);
      const name = document.getText(wordRange);

      const predefinedFilePath = path.join(__dirname, '@types', 'tampermonkey.d.ts');

      const doc = await vscode.workspace.openTextDocument(vscode.Uri.file(predefinedFilePath));

      for (let line = 0; line < doc.lineCount; line++) {
        const lineText = doc.lineAt(line).text;
  
        if (lineText.includes(`function ${name}`)) {
          return new vscode.Location(vscode.Uri.file(predefinedFilePath), new vscode.Position(line, 0));
        }
      }
  
      return null;
    }
  }));

	context.subscriptions.push(
    ...commandDisposables,
    ...configDisposables,
    ...hoverProviders,
    ...completionItemProviders,
    ...signatureHelpProviders,
    ...definitionProviders,
    ...typeDefinitionProviders
  );
}

/** Deactivate the extension */
export function deactivate() {
  vscode.window.showInformationMessage('Your extension "Monkey Wrench" is now deactivated!');
}

/** @type {vscode.Disposable[]} */
const commandDisposables = [];

/** @type {vscode.Disposable[]} */
const configDisposables = [];

/** @type {vscode.Disposable[]} */
const hoverProviders = [];

/** @type {vscode.Disposable[]} */
const completionItemProviders = [];

/** @type {vscode.Disposable[]} */
const signatureHelpProviders = [];

/** @type {vscode.Disposable[]} */
const definitionProviders = [];

/** @type {vscode.Disposable[]} */
const typeDefinitionProviders = [];

const config = vscode.workspace.getConfiguration('monkey-wrench');

/**
 * Check if the document should be processed
 * @param {vscode.TextDocument} document - The document to check
 * @returns {boolean} Whether the document should be processed
 * @example
 *   allSystemsGo(document);
 */
const allSystemsGo = document => {
  const activationCondition = config.get('activationCondition');

  const fileExtensionCheckPassed = (() => {
    const activeFileExtensions = activationCondition.fileExtensions;
    if (!activeFileExtensions.length) return true;
    const fileExt = document.fileName.split('.').filter(Boolean).toSpliced(0, 1, '').join('.');
    return activeFileExtensions.includes(fileExt);
  })();

  const userscriptHeaderCheckPassed = (() => {
    if (!activationCondition.userscriptHeader) return true;
    return /\/\/\s==UserScript==/.test(document.lineAt(0).text.trim());
  })();

  return fileExtensionCheckPassed && userscriptHeaderCheckPassed;
};

// apiItems
/** @type {vscode.CompletionItemProvider<vscode.CompletionItem>} */
const ApiCompletionItemProvider = {
  provideCompletionItems(document, position, token, context) {
    if (!allSystemsGo(document)) return;
    let wordRange = document.getWordRangeAtPosition(position);
    if (!wordRange) return;
    const word = document.getText(wordRange);
    return apiItems
      .filter(item => item.label.startsWith(word) && item.completions.length)
      .map(item => item.completions)
      .flat()
      .filter(x => x.kind === vscode.CompletionItemKind.Function || vscode.CompletionItemKind.Variable);
  }
};

// metadataItems
/** @type {vscode.CompletionItemProvider<vscode.CompletionItem>} */
const MetadataCompletionItemProvider = {
  provideCompletionItems(document, position, token, context) {
    if (!allSystemsGo(document)) return;
    let wordRange = document.getWordRangeAtPosition(position);
    if (!wordRange) return;
    const word = document.getText(wordRange);
    return metadataItems
      .filter(item => item.label.startsWith(word) && item.completions.length)
      .map(item => item.completions)
      .flat()
      .filter(x => x.kind === vscode.CompletionItemKind.Field || x.kind === vscode.CompletionItemKind.Snippet)
  }
};

// metadataItems (parameters)
/** @type {vscode.CompletionItemProvider<vscode.CompletionItem>} */
const MetadataParametersCompletionItemProvider = {
  provideCompletionItems(document, position, token, context) {
    if (!allSystemsGo(document)) return;
    const line = document.lineAt(position.line).text;
    if (line.startsWith('// @')) {
      const match = line.match(/@\w+/);
      let parameterIndex = line
        .substring(match[0].length + match.index)
        .replaceAll(/\s+/g, '|')
        .split('|').length - 2;
      if (parameterIndex > 0) return;
      return metadataItems
        .filter(item => item.label.startsWith(match[0]) && item.completions.length)
        .map(item => item.completions)
        .flat()
        .filter(x => x.kind === vscode.CompletionItemKind.Value)
    }
  }
};
