/** @type {StripIndent.Create} */
function createStripIndent(options) {

  /** @type {StripIndent} */
  const stripIndent = (strings, ...values) => {
    const { escapeSpecialCharacters = false } = options;

    const lines = [...strings]
      .reduce((prev, current, i) => prev + current + (values[i] || ''), '')
      .split('\n')
      .filter(line => line.trim() !== '');

    const minIndent = Math.min(...lines.map(line => line.match(/^\s*/)[0].length));

    return lines
      .map(line => {
        line = line.substring(minIndent);
        if (!escapeSpecialCharacters) return line;
        return line
          .replace(/\\\n[ \t]*/g, '')
          .replace(/\\`/g, '`')
          .replace(/\\\$/g, '$')
          .replace(/\\\{/g, '{');
      })
      .join('\n');
  }

  stripIndent.withOptions = newOptions => createStripIndent({ ...options, ...newOptions });

  return stripIndent;
}

const stripIndent = createStripIndent({});

export default stripIndent;
