import * as vscode from 'vscode';

const config = vscode.workspace.getConfiguration('monkey-wrench');
const metadataConfig = config.get('metadata');
const metadata = new vscode.SnippetString()
  .appendText('// ==UserScript==')
  .appendText('\n// @name         ').appendPlaceholder('Script name')
  .appendText('\n// @namespace    ').appendPlaceholder(metadataConfig.namespace)
  .appendText('\n// @version      ').appendPlaceholder('0.1')
  .appendText('\n// @description  ').appendPlaceholder('A brief summary to describe the script')
  .appendText('\n// @author       ').appendPlaceholder(metadataConfig.author)
  .appendText('\n// @match        ').appendPlaceholder('*://*/*')
  .appendText('\n// @icon         ').appendPlaceholder(metadataConfig.icon)
  .appendText('\n// @grant        ').appendPlaceholder('none')
  .appendText('\n// ==/UserScript==')
  .appendText('\n')
  .appendText('\n(function () {')
  .appendText('\n\t\'use strict\';')
  .appendText('\n\t').appendTabstop(0)
  .appendText('\n})();');

const grantChoices = [
  'none',
  'unsafeWindow',
  'window.onurlchange',
  'window.close',
  'window.focus',
  'GM_addElement',
  'GM.addElement',
  'GM_addStyle',
  'GM.addStyle',
  'GM_download',
  'GM.download',
  'GM_getResourceText',
  'GM.getResourceText',
  'GM_getResourceURL',
  'GM.getResourceURL',
  'GM_info',
  'GM.info',
  'GM_log',
  'GM.log',
  'GM_notification',
  'GM.notification',
  'GM_openInTab',
  'GM.openInTab',
  'GM_registerMenuCommand',
  'GM.registerMenuCommand',
  'GM_unregisterMenuCommand',
  'GM.unregisterMenuCommand',
  'GM_setClipboard',
  'GM.setClipboard',
  'GM_getTab',
  'GM.getTab',
  'GM_saveTab',
  'GM.saveTab',
  'GM_getTabs',
  'GM.getTabs',
  'GM_setValue',
  'GM.setValue',
  'GM_getValue',
  'GM.getValue',
  'GM_deleteValue',
  'GM.deleteValue',
  'GM_listValues',
  'GM.listValues',
  'GM_addValueChangeListener',
  'GM.addValueChangeListener',
  'GM_removeValueChangeListener',
  'GM.removeValueChangeListener',
  'GM_xmlhttpRequest',
  'GM.xmlHttpRequest',
  'GM_webRequest',
  'GM.webRequest',
  'GM_cookie',
  'GM.cookie'
];
const grant = new vscode.SnippetString()
  .appendText('@grant ')
  .appendChoice(grantChoices);

const antifeatureChoices = [
  'ads',
  'tracking',
  'miner'
];
const antifeature = new vscode.SnippetString()
  .appendText('@antifeature ')
  .appendChoice(antifeatureChoices);

const runAtChoices = [
  'document-start',
  'document-body',
  'document-end',
  'document-idle',
  'context-menu'  
];
const runAt = new vscode.SnippetString()
  .appendText('@run-at ')
  .appendChoice(runAtChoices);

const sandboxChoices = [
  'raw',
  'JavaScript',
  'DOM'
];
const sandbox = new vscode.SnippetString()
  .appendText('@sandbox ')
  .appendChoice(sandboxChoices);

const webRequest = new vscode.SnippetString()
  .appendText('@webRequest { "selector": ')
  .appendPlaceholder('selector')
  .appendText(', "action": ')
  .appendPlaceholder('action')
  .appendText(' }');

const addElement1 = new vscode.SnippetString()
  .appendText('GM_addElement(')
  .appendPlaceholder('parentNode')
  .appendText(', ')
  .appendPlaceholder('tagName')
  .appendText(', {\n\t')
  .appendTabstop()
  .appendText('\n});');

const addElement2 = new vscode.SnippetString()
  .appendText('GM_addElement(')
  .appendPlaceholder('tagName')
  .appendText(', {\n\t')
  .appendTabstop()
  .appendText('\n});');

export {
  metadata,
  grant,
  antifeature,
  runAt,
  sandbox,
  webRequest,
  addElement1,
  addElement2
};
