import { existsSync, readFileSync, writeFileSync } from 'fs';
import { join } from 'path';
import * as vscode from 'vscode';

/**
 * Update configuration file
 * @param {String} configName - Configuration file name
 * @example
 *   updateConfig('jsconfig');
 */
export function updateConfig(configName) {
  const workspaceFolders = vscode.workspace.workspaceFolders;
  if (workspaceFolders && workspaceFolders.length > 0) {
    const workspaceRoot = workspaceFolders[0].uri.fsPath;
    const configPath = join(workspaceRoot, `${configName}.json`);

    if (existsSync(configPath)) {
      try {
        const config = JSON.parse(readFileSync(configPath, 'utf8'));

        if (configName === 'jsconfig' || configName === 'tsconfig') {
          if (!config.compilerOptions) {
            config.compilerOptions = {};
          }
          if (!config.compilerOptions.typeRoots) {
            config.compilerOptions.typeRoots = [];
          }

          config.compilerOptions.typeRoots.push('./node_modules/@types');
          config.compilerOptions.typeRoots.push('./src/@types');
        } else if (configName === '.eslintrc') {
          if (!config.env) {
            config.env = {};
          }

          config.env.greasemonkey = true;
        }

        writeFileSync(configPath, JSON.stringify(config, null, 2));
        console.log(`\`${configName}.json\` updated successfully.`);
      } catch (err) {
        vscode.window.showErrorMessage(`Error updating \`${configName}.json\`: ${err}`, 'OK', 'Retry').then(selection => {
          if (selection === 'Retry') updateConfig(configName);
        });
      }
    } else {
      vscode.window.showErrorMessage(`No \`${configName}.json\` found.`, 'OK', 'Create').then(selection => {
        if (selection === 'Create') {
          let configObject;
          if (configName === 'jsconfig' || configName === 'tsconfig') {
            configObject = {
              compilerOptions: {
                typeRoots: [
                  './node_modules/@types',
                  './src/@types'
                ]
              }
            };
          } else if (configName === '.eslintrc') {
            configObject = {
              env: {
                greasemonkey: true
              }
            };
          }
          writeFileSync(configPath, JSON.stringify(configObject, null, 2));
          vscode.window.showInformationMessage(`\`${configName}.json\` created successfully.`);
          updateConfig(configName);
        }
      });
    }
  } else {
    vscode.window.showErrorMessage('No workspace folder found.', 'OK', 'Create').then(selection => {
      if (selection === 'Create') {
        vscode.commands.executeCommand('workbench.action.saveWorkspaceAs').then(() => updateConfig(configName));
      }
    });
  }
}